.navbar {	
    height: 60px;	
    display: flex;	
    justify-content: center;	
    align-items: center;	
    font-size: 1.2rem;	
    font-family: 'CherishToday';
    width:100%;
    position: absolute;
    z-index: 999;
    margin-top: -20px
  }

  .navbar-fixed {	
    height: 60px;	
    display: flex;	
    justify-content: center;	
    align-items: center;	
    font-size: 1.2rem;	
    font-family: 'CherishToday';
    width:100%;
    position: fixed;
    z-index: 999;
    margin-top: -20px
  }

  .left{
    float:left;
  }
  .right{
    float:right;
  }
  
  .navbar-container {	
    display: flex;	
    justify-content: center;	
    align-items: center;	
    height: 80px;	

  }	
  
  .navbar-logo {	
    color: #fff;	
    justify-self: start;	
    margin-left: 20px;	
    cursor: pointer;	
    text-decoration: none;	
    font-size: 25px;	
    display: flex;	
    align-items: center;	
    font-family: 'CherishToday';
    font-size: 35px;
    position: absolute;
    left: 10px;
    margin-top: 10px;
  }	
  
  .fa-typo3 {	
    margin-left: 0.5rem;	
    font-size: 1.8rem;	
  }	
  
  .nav-menu {	
    display: grid;	
    grid-template-columns: repeat(4, auto);	
    list-style: none;	
    text-align: center;	
    justify-content: end;	
    margin-right: 2rem;	
    position: absolute;
    right: 10px;
  }	
  
  .nav-item {	
    height: 60px;	
  }	
  
  .nav-links {	
    color: #fff;	
    display: flex;	
    align-items: center;	
    text-decoration: none;	
    padding: 0.5rem 1rem;	
    height: 60px;	
    font-family: 'CherishToday';
    font-size: 30px;
  }	
  
  .nav-links:hover {	
    border-bottom: 4px solid #fff;	
    transition: all 0.2s ease-out;	
  }	
  
  .fa-bars {	
    color: #fff;	
  }	
  
  .nav-links-mobile {	
    display: none;	
  }	
  
  .menu-icon {	
    display: none;	
  }	
  
  .is-active{
    border-bottom: 4px solid #fff;	
  }
  
  @media screen and (max-width: 960px) {	
    .NavbarItems {	
      position: relative;	
    }
  
    .nav-menu {	
      display: flex;	
      flex-direction: column;	
      width: 100%;	
      height: calc(100vh - 60px);	
      position: absolute;	
      top: 60px;	
      left: -100%;	
      opacity: 1;	
      transition: all 0.5s ease;	
      font-family: 'CherishToday';
      margin-top: 20px;
    }	
    
    .nav-menu.active {	
      background: linear-gradient(to top right, #2566ae, #65d3fa);
      left: 0px;	
      transition: all 0.5s ease;	
      z-index: 1;	
      padding: 0;
    }	
  
    .nav-links {	
      text-align: center;	
      width: 100%;	
      display: table;	
      padding: 15px;
      justify-content: center;
    }	
  
    .nav-links:hover {	
      background-color: #fff;	
      color: #242424;	
      border-radius: 0;	
    }	
  
    .navbar-logo {	
      position: absolute;	
      top: 0;	
      left: 0;	
      transform: translate(25%, 40%);	
    }	
  
    .menu-icon {	
      display: block;	
      position: absolute;	
      top: 0;	
      right: 0;	
      transform: translate(-100%, 30%);	
      font-size: 1.8rem;	
      cursor: pointer;	
      margin-top: 20px;
    }	
  
    .fa-times {	
      color: #fff;	
      font-size: 2rem;	
    }	
  
    .nav-links-mobile {	
      display: block;	
      text-align: center;	
      margin: 2rem auto;	
      border-radius: 4px;	
      width: 80%;	
      text-decoration: none;	
      font-size: 1.5rem;	
      background-color: transparent;	
      color: #fff;	
      padding: 14px 20px;	
      border: 1px solid #fff;	
      transition: all 0.3s ease-out;	
    }	
  
    .nav-links-mobile:hover {	
      background: #fff;	
      color: #242424;	
      transition: 250ms;	
    }	
  }
